@use "variables" as var;

@include var.line;

#email-form{
    margin-top: 5%;
    display: flex;
    justify-content: center;
    form{
        width: 50%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 0.5rem;
        *{
            width: 100%;
            border: none;
            font-size: 1rem;
        }
        #nameAndEmail{
            display: flex;
            gap: 1em;
            height: 2.5em;
            #name{
                flex: 1;
            }
            #email{
                flex: 1;
            }
        }
        #subject{
            height: 2em;
        }
        #message-body{
            height: 10em;
            resize: none;
        }
        #submit-btn-contact{
            max-width: fit-content;
            background-color: var.$color-secondary;
            color: white;
            border-radius: var.$radius;
        }
    }
}
#links{
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    a{
            img{
                width: 100%;
                max-width: 17em;
            }
        }
}

@media(max-width: 767px){
    #email-form{
        margin-top: 5%;
        display: flex;
        justify-content: center;
        form{
            width: 75%;
            #message-body{
                height: 7.5em;
                resize: none;
            }
            #submit-btn-contact{
                max-width: fit-content;
                background-color: var.$color-secondary;
                color: white;
                border-radius: var.$radius;
            }
        }
    }
    #links{
        a{
                img{
                    max-width: 10em;
                }
            }
    }
}