@use "variables" as var;

    h2{
        color:var.$color-secondary;
        text-align: center;
    }
    @include var.line;
    #education{
        display: flex;
        justify-content: space-between;
        #edu-2{
            margin-left: 2em;
        }
        h3{
        color:var.$color-secondary;
        }
    }
    .hobbies{
        &-svg{
            width: 100%;
            margin: 20px;
            display: flex;
            justify-content: space-between;
            .img-and-txt{
            margin-right: 1em;
                display: flex;
                justify-content: center;
                align-items: center;
                flex-direction: column;
                h4{
                    color: var.$color-secondary;
                    text-align: center;
                }
                img{
                min-width: 8em;
                max-width: 10em;

                }
    }
    }
    }
    @media(max-width: 1100px){
        .hobbies{
            &-svg{
                margin: 0;
                .img-and-txt{
                margin-right: 0;
                    img{
                    min-width: 1em;
                    max-width: 5em;
    
                    }
        }
        }
        }
    }